@import "../../common/util/variables";

.content {
    margin:5%;
   
    &__container {
        background: $color-secondary-2;
        border-radius: $border-radius-2;
        padding: 2rem;

        &__image {
            img {
                width: 100%;
                height: auto;
            }
        }
        &__details{
            display: flex;
            align-items: center;
            h2 {
                color: $color-primary-2;
            }
            @media screen and (max-width: $screen-xs) {
                text-align: center;
            }
        }
    }
    &__recent-projects{
        margin-top: 5%;

        h2 {
            color: $color-primary-3;
        }
        &__header{
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        
    }

    &__strengths{
        margin-top: 7%;
        text-align: center;
         
        &__header{
           color: $color-primary-3; 
        }
        .strengths{
            margin: 5% 2%;
            &__item{
                &__image {
                    img {
                        width:100%;
                        height:auto;
                        background: $color-secondary-5;
                        border-radius: $border-radius-5;

                    }
                }
               &__content{
                   margin-top:10%;
                   h3 {
                       color: $color-primary-7;
                   }
               } 
            }
        }
    }
}